@media (min-width: 768px) {
  .header__inner {
    display: flex;
    flex-direction: row;
    width: auto;
    height: 88px;
    align-items: center;
    justify-content: space-between;
    gap: 20px; }
  .header__logo::after {
    display: none; }
  .header__logo::before {
    content: '';
    position: absolute;
    height: 32px;
    width: 2px;
    margin-left: -15px;
    margin-top: 15px;
    background-color: #eaeaea; }
  .button-hidder {
    display: flex;
    gap: 10px; }
  .button-hidder::after {
    content: '';
    background-color: #eaeaea;
    height: 32px;
    width: 2px;
    align-self: center; }
  .main__about-block {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: auto;
    gap: 15px;
    flex-wrap: nowrap;
    overflow: hidden; }
  .main__info {
    width: 50%;
    display: flex;
    flex-direction: column; }
  .main__text-control {
    height: 175px; }
  .main__readmore-block {
    margin-top: 15px; }
  .main__image {
    flex-grow: 1;
    margin-right: 20px; }
  .swiper-container,
  .swiper-container1 {
    width: 768px;
    height: auto; }
  .brand-wrapper {
    height: 160px;
    margin-top: 10px;
    margin-left: 10px;
    display: grid;
    grid-template-columns: repeat(3, 224px);
    grid-template-rows: repeat(2, 72px);
    grid-auto-rows: 0;
    overflow-y: hidden;
    gap: 24px;
    row-gap: 10px; }
  .swiper__btn-show {
    display: block;
    border: 1px solid #ff3e79;
    border-radius: 50%;
    background: url(./static/dropdown.svg) no-repeat center;
    height: 40px;
    width: 40px; }
  .brand-showMore,
  .hardware-showMore {
    display: block;
    text-decoration: none;
    color: #1b1c21;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    font-weight: 550;
    border: none;
    background: transparent;
    margin: 20px 0;
    margin-left: 25px;
    align-self: start; }
  .show-more::before,
  .show-more1::before {
    content: '';
    display: inline-block;
    width: 24px;
    height: 24px;
    vertical-align: middle;
    background: url(./static/link-icon.png) no-repeat 50% 50%; }
  .wrapper-show {
    height: 90%;
    margin-top: 10px;
    margin-left: 10px;
    display: grid;
    grid-template-columns: repeat(3, 224px);
    grid-auto-rows: 72px;
    overflow-y: hidden;
    gap: 24px;
    row-gap: 10px; }
  .hardware-wrapper {
    height: 160px;
    margin-top: 10px;
    margin-left: 10px;
    display: grid;
    grid-template-columns: repeat(3, 224px);
    overflow-y: hidden;
    gap: 24px;
    row-gap: 10px; }
  .slide-hardware {
    width: 240px;
    height: 160px;
    background-repeat: no-repeat;
    background-position: 20px center;
    border-radius: 6px;
    border: 1px solid #eaeaea; }
  .wrapper-show1 {
    height: 90%;
    margin-top: 10px;
    margin-left: 10px;
    display: grid;
    grid-template-columns: repeat(3, 224px);
    grid-auto-rows: 160px;
    overflow-y: hidden;
    gap: 24px;
    row-gap: 10px; }
  .services-content {
    display: none; }
  .services-table {
    display: table;
    width: 100%; }
  .services__tablet {
    width: 100%;
    border-collapse: collapse; }
  .services__title {
    text-align: left;
    height: 70px;
    font-size: 14px;
    color: #7e7e82; }
    .services__title .services__list {
      padding-left: 10px; }
  .services__body {
    background-color: #ffffff;
    border-radius: 16px;
    width: auto; }
  .services__wrap {
    text-align: left;
    font-size: 14px;
    height: 60px; }
  .services__item {
    padding-top: 20px;
    border-bottom: 1px solid #eaeaea;
    vertical-align: top; }
  .item--name {
    padding-left: 10px; }
  .last--item {
    border-bottom: none; }
  .item--lining {
    text-align: end;
    vertical-align: middle;
    padding-bottom: 10px;
    padding-right: 10px; }
  .services__item-btn {
    display: inline-block;
    border-radius: 16px;
    background-color: #ff3e79;
    min-height: 32px;
    width: 118px;
    color: #fff;
    font-size: 14px;
    line-height: 24px; }
  .services__price-info {
    margin-top: 20px;
    width: 420px; }
  .services__offer {
    width: 100%; }
  .footer-info {
    width: 100%;
    margin-top: 20px;
    height: 80px;
    border-top: 1px solid #d9fff5;
    text-align: center;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    white-space: normal; }
  .footer-info__string {
    font-size: 14px;
    padding: 0 20px;
    padding-top: 30px;
    text-align: right; }
  .string--left {
    text-align: left; }
  .string--right {
    text-align: right;
    word-wrap: break-word;
    width: 200px; } }
