body {
  font-family: 'TT Lakes', 'Arial', sans-serif;
  color: #1b1c21; }

.lock {
  overflow: hidden; }

.brand-text {
  color: #1b1c21; }

.open--menu:hover {
  opacity: 0.7;
  transform: rotateZ(45deg) scale(1.1);
  transition: 0.5s; }

.open--menu--search:hover {
  opacity: 0.5;
  transform: scale(1.1);
  transition: 0.5s; }

.round-icon:active {
  transition: box-shadow 0.1s;
  opacity: 1;
  box-shadow: 2px 2px 5px #ff3e79; }

.button-bot:hover,
.round-icon:hover {
  opacity: 0.5;
  transform: scale(1.1);
  transition: 0.5s; }

.button-bot:active {
  opacity: 1;
  box-shadow: 2px 2px 5px #ff3e79;
  transition: 0.1s; }

.menu__link:hover {
  color: #7e7e82; }

.menu__link:hover::before {
  content: '';
  width: 4px;
  height: 34px;
  background: url(./static/highlight-2.svg) no-repeat center;
  position: absolute;
  left: 0;
  top: auto;
  opacity: 0.7;
  margin-top: -5px; }

.brand-wrap:hover,
.hardware__link:hover {
  transform: scale(1.1); }

.btn--pretty:hover {
  box-shadow: 0 0 40px 40px #ffadad inset;
  transition: box-shadow 500ms linear 0ms; }
